<template>
  <div class="user-wrap">
    <van-nav-bar
      title="二维码"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <img 
      class="qscode-img"
      v-for="(img, index) in imageList" 
      :key="index" 
      :src="img"
    />
  </div>
</template>

<script>
export default {
  name: 'questions',
  components: {
  },
  data () {
    return {
      imageList: [
        'https://img.yzcdn.cn/vant/apple-1.jpg',
        'https://img.yzcdn.cn/vant/apple-2.jpg'
      ]
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.push({ path: '/user' })
    }
  }
}
</script>

<style scoped lang="less">
  .qscode-img {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 6px;
    margin: 20px 4% 0;
    box-sizing: border-box;
    width: 92%;
  }
</style>


